<template>
<div>
  <input type="text" disabled id="scrollLeftBox">
  <div class="btnwrap">
    <el-button type="success" plain size="mini" @click="newPostVisible = true" :disabled="subPostView">发帖</el-button>
    <el-button type="warning" plain size="mini" v-if="subPostView" @click="rePostVisible =true">回复</el-button>
  </div>

  <div class="postwrap" v-show="!subPostView">
    <div class="classwrap">
      <div class="classbtn">筛选器:</div>
      <span class="classbtn" style="background-color: #66b1ff" @click="selectCategory(0)">工科</span>
      <span class="classbtn" style="background-color: #3ca758" @click="selectCategory(1)">理科</span>
      <span class="classbtn" style="background-color: #a6b338" @click="selectCategory(2)">文科</span>
      <span class="classbtn" style="background-color: #dc749f" @click="selectCategory(3)">灌水</span>
      <span class="classbtn" style="background-color: #e20739" @click="selectCategory(4)">建议</span>
      <input type="text"  style="width:100px;margin-right:8px" placeholder="只看该话题主" v-model="selAuthor">
      <el-button icon="el-icon-search" size="mini" style="margin-right:50px" circle @click="selectAuthor"></el-button>
      <span class="clearbtn" @click="clearSel">清除筛选</span>
    </div>
    <div class="infinite-list" style="overflow:auto;height:475px" v-infinite-scroll="load" infinite-scroll-disabled="disabled" infinite-scroll-distance="1">
      <div class="post" v-for="(post,index) in postArray" :key="index">
        <div class="post_class" v-if="post.category==0" style="background-color: #66b1ff">工科</div>
        <div class="post_class" v-if="post.category==1" style="background-color: #3ca758">理科</div>
        <div class="post_class" v-if="post.category==2" style="background-color: #a6b338">文科</div>
        <div class="post_class" v-if="post.category==3" style="background-color: #dc749f">灌水</div>
        <div class="post_class" v-if="post.category==4" style="background-color: #e20739">建议</div>
        <div class="post_title" @click="intoPostDetail(post)" @mouseover="hoverIndex=index" @mouseleave="hoverIndex=-1" :class="{'title_hover':index==hoverIndex}">{{post.title}}</div>
        <div class="post_author">话题主：{{post.author}}</div>
        <div class="post_time">发表时间：{{timeFormat(post.postTime)}}</div>
        <div class="last_time">最后回复：{{timeFormat(post.lastTime)}}</div>
      </div>
      <p v-if="loading">加载中...</p>
      <p v-if="noMore">没有更多了</p>
    </div>
  </div>

  <div class="postwrap" v-show="subPostView">
    <div class="sub_wrap">
      <div class="sub_back" @click="backToPostList"><i class="el-icon-arrow-left"></i>返回</div>
      <div class="sub_title">{{subPost.title}}</div>
      <div class="sub_content" style="height:50px">{{subPost.content}}</div>
      <div class="sub_time">发表时间：{{timeFormat(subPost.postTime)}}</div>
      <div class="sub_author">话题主：{{subPost.author}}</div>
    </div>
    <div class="infinite-list" style="overflow-y: auto; height: 390px;" v-infinite-scroll="loadSub" infinite-scroll-disabled="disabledSub" infinite-scroll-distance="1">
      <div v-for="(reply,index) in subPostReply" :key="index" style="border-bottom: 1px solid #fff; float:left">
        <div class="sub_content">{{reply.content}}</div>
        <div class="sub_time">回复时间：{{timeFormat(reply.postTime)}}</div>
        <div class="sub_author">回帖人：{{reply.author}}</div>
      </div>
      <p v-if="loadingSub">加载中...</p>
      <p v-if="noMoreSub">没有更多了</p>
    </div>
  </div>

  <el-dialog title="发表新话题" :visible.sync="newPostVisible">
    <el-input placeholder="请输入标题" class="input-with-select" v-model="postTitle">
      <el-select v-model="classSel" slot="prepend" style="width:80px" @change="newPostViewClassSel">
        <el-option label="工科" value="0"></el-option>
        <el-option label="理科" value="1"></el-option>
        <el-option label="文科" value="2"></el-option>
        <el-option label="灌水" value="3"></el-option>
        <el-option label="建议" value="4"></el-option>
      </el-select>
    </el-input>
    <el-input style="margin:5px 0" type="textarea" :rows="4" placeholder="请输入内容" v-model="postContent"></el-input>
    <el-input placeholder="话题主昵称" style="width:200px; float:left" v-model="postAuthor"></el-input>
    <div slot="footer" class="dialog-footer">
      <el-button @click="newPostVisible = false">取 消</el-button>
      <el-button type="primary" @click="newPostCommit">发 表</el-button>
    </div>
  </el-dialog>

  <el-dialog title="回复该话题" :visible.sync="rePostVisible">
    <el-input style="margin:5px 0" type="textarea" :rows="4" placeholder="请输入内容" v-model="reContent"></el-input>
    <el-input placeholder="回帖人昵称" style="width:200px; float:left" v-model="reAuthor"></el-input>
    <div slot="footer" class="dialog-footer">
      <el-button @click="rePostVisible = false">取 消</el-button>
      <el-button type="primary" @click="rePostCommit">发 表</el-button>
    </div>
  </el-dialog>

</div>
</template>

<script>
import axios from 'axios'
const HOST = 'http://www.scicool.club'
export default {
  name: 'forum',
  data() {
    return {
      page: 1,
      pageSub: 1,
      hoverIndex: -1,    //鼠标悬停变色,标记哪一个
      postArray:[],
      loading: false,
      loadingSub: false,
      subPost:{},
      subPostReply:[],
      subPostView: false,
      selAuthor:'',
      selCatgory:'',  
      newPostVisible: false,      //发帖弹窗 
      classSel: '工科',
      classSelValue: "0",
      postContent: '',
      postTitle:'',
      postAuthor:'',
      rePostVisible: false,     //回复弹窗
      reContent: '',
      reAuthor:'',
    }
  },
  mounted(){
    this.initNotice()
    this.initPostList()
  },
  computed: {
    noMore () {       //获取条目长度
      return this.postArray.length < this.page*10
    },
    disabled () {
      return this.loading || this.noMore
    },
    noMoreSub(){
      return this.subPostReply.length < this.pageSub*10
    },
    disabledSub(){
      return this.loadingSub || this.noMoreSub
    }
  },
  methods: {
    initPostList(){
      this.getPostList(1)
    },
    getPostList(page){
      this.loading =true
      axios.get(HOST+'/api/posts/getPostList',{
        params:{
          page :page,
          author:this.selAuthor,
          category:this.selCatgory
        }
      }).then(res=>{
        if(res.data.code==200){
          this.postArray.push(...res.data.data)
          this.loading = false
        }else{
          console.log(res.data.data)
          alert('网络错误'+res.data.code)
        }
      })
    },
    getSubReList(pid,page){
      this.loadingSub =true
      axios.get(HOST+'/api/subPosts/getReList',{
        params:{
          page :page,
          pid: pid
        }
      }).then(res=>{
        if(res.data.code==200){
          this.subPostReply.push(...res.data.data)
          this.loadingSub = false
        }else {
          console.log(res.data.data);
          alert('网络错误'+res.data.code)
        }
      })
    },
    initNotice(){
      let str = '1、交流区无需注册，可以自由留言；2、请自觉遵守网络文明公约，严禁攻击诋毁、低俗色情等内容；3、保持学术纯洁，莫谈国是；4、点击上面的“神秘花园”可能会有意外收获；5、欢迎发表对网站的意见建议；';
      let scrollLeftBox = document.getElementById('scrollLeftBox');               
      setInterval(()=>{
        scrollLeftBox.value = str;
        str = str.substring(1, str.length) +　str.substring(0, 1);
      }, 1000);
    },
    newPostCommit(){              //发布axios
      this.newPostVisible = false
      const postData ={
        category:this.classSelValue,
        title:this.postTitle,
        content:this.postContent,
        author:this.postAuthor,
      }
      axios.post(HOST+'/api/posts/submitPost',postData).then(res=>{
        if(res.data.code==200){
          this.postArray=[]
          this.postContent=''
          this.postTitle=''
          this.postAuthor=''
          this.getPostList(1)
        }else{
          console.log('网络错误'+res.data.code);
        }
      })
    },
    rePostCommit(){
      this.rePostVisible =false
      axios.post(HOST+'/api/subPosts/submitRe',{
        pid:this.subPost._id,
        content:this.reContent,
        author:this.reAuthor
      }).then(res=>{
        if(res.data.code==200){
          axios.post(HOST+'/api/posts/updateLastTime',{
            pid:this.subPost._id
          }).then(()=>{
            this.subPostReply=[]
            this.reContent=''
            this.reAuthor=''
            this.getSubReList(this.subPost._id,1)
          })
        }else{
          console.log('网络错误'+res.data.code);
        }
      })
    },
    newPostViewClassSel(val){       
      this.classSelValue = val
    },
    selectCategory(cat){
      this.selCatgory=cat
      this.postArray=[]
      this.getPostList(1)
    },
    selectAuthor(){
      if (this.selAuthor){
        this.postArray=[]
        this.getPostList(1)
      }else{
        alert('还没输入话题主的名字呢')
      }
    },
    clearSel(){
      this.selCatgory=''
      this.selAuthor=''
      this.postArray=[]
      this.getPostList(1)
    },
    load () {     //axios获取新的条目，第1页，push到array中
      this.page++
      this.loading =true
      this.getPostList(this.page)
    },
    loadSub(){
      this.pageSub++
      this.getSubReList(this.subPost._id,this.pageSub)
    },
    intoPostDetail(post){
      this.subPost =post
      this.subPostView =true
      this.getSubReList(post._id,1)
    },
    backToPostList(){
      this.subPostView =false
      this.subPostReply =[]
      this.subPost ={}
      this.pageSub =1
    },
    timeFormat(isodate){
      isodate = new Date(isodate)
      let year = isodate.getFullYear()
      let month = isodate.getMonth()+1
      let date = isodate.getDate()
      let hours = isodate.getHours()
      let min = isodate.getMinutes()
      let sec = isodate.getSeconds()
      return year+'-'+month+'-'+date+' '+hours+':'+min+':'+sec
    }
  }
}
</script>

